@import url(https://fonts.googleapis.com/css?family=Rubik:400,400italic,300,700);
html {
    box-sizing: border-box;
    font-size: 10px;
}

*, *::after, *::before {
    box-sizing: inherit;
}

body {
    min-width: 32rem;
    font-family: 'Rubik', sans-serif;
    background: #fff;
    overflow-x: hidden;
}

@media only screen and (min-width: 992px) {
    .nopadding {
        padding: 0 !important;
        margin: 0 !important;
    }
}

.cases:before, .contacts:before, .footer:before {
    position: absolute;
    display: block;
    content: '';
    top: -5rem;
    height: 10rem;
    border-radius: 50%;
}
@media only screen and (min-width: 320px) {
    .cases:before, .contacts:before, .footer:before {
        width: 120%;
        left: -10%;
    }
}
@media only screen and (min-width: 768px) {
    .cases:before, .contacts:before, .footer:before {
        width: 110%;
        left: -5%;
    }
}
@media only screen and (min-width: 992px) {
    .cases:before, .contacts:before, .footer:before {
        width: 104%;
        left: -2%;
    }
}

.show {
    display: block !important;
    opacity: 1 !important;
}

.hide {
    opacity: 0;
}

p, h1, h2, h3, h4, h5, h6, a, li {
    font-size: 1.6rem;
    font-weight: 400;
    color: #504f4f;
    margin: 0 0 1rem;
}

h1 {
    font-size: 6.5rem;
    font-weight: 400;
}

h2 {
    font-size: 5rem;
}

h3 {
    font-size: 3rem;
}

h4 {
    font-size: 2.2rem;
}

h5 {
    font-size: 1.8rem;
}

h6 {
    font-size: 1.6rem;
}

a {
    -webkit-transition: all 0.2s ease-in-out;
       -moz-transition: all 0.2s ease-in-out;
        -ms-transition: all 0.2s ease-in-out;
         -o-transition: all 0.2s ease-in-out;
            transition: all 0.2s ease-in-out;
}

.header {
    position: relative;
    width: 100%;
    height: 95.3rem;
}
@media only screen and (min-width: 320px) {
    .header {
        height: auto;
        padding-top: 2rem;
        background: #9b1a48;
        padding-bottom: 15rem;
    }
}
@media only screen and (min-width: 768px) {
    .header {
        height: 95.3rem;
        background: url('../images/bg-pink.jpg') no-repeat top center;
        background-size: 100% 95.3rem;
        padding-top: 3rem;
    }
}
@media only screen and (min-width: 992px) {
    .header {
        padding-top: 5.3rem;
    }
}
.header__content h1 {
    font-weight: 300;
    text-transform: uppercase;
    color: #fff;
    margin-bottom: 3rem;
}
@media only screen and (min-width: 320px) {
    .header__content h1 {
        font-size: 4.0625rem;
        line-height: 4.375rem;
    }
}
@media only screen and (min-width: 768px) {
    .header__content h1 {
        font-size: 5rem;
        line-height: 5.38462rem;
    }
}
@media only screen and (min-width: 992px) {
    .header__content h1 {
        font-size: 6.5rem;
        line-height: 7rem;
    }
}
.header__content h1 span {
    display: block;
    color: #ff87b8;
}
@media only screen and (min-width: 320px) {
    .header__content h1 span {
        font-size: 3.125rem;
        line-height: 4.375rem;
    }
}
@media only screen and (min-width: 768px) {
    .header__content h1 span {
        font-size: 3.84615rem;
        line-height: 5.38462rem;
    }
}
@media only screen and (min-width: 992px) {
    .header__content h1 span {
        font-size: 5rem;
        line-height: 7rem;
    }
}
.header__content p {
    max-width: 42rem;
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 2.4rem;
    color: #fff;
    margin-bottom: 4rem;
}
.header__link {
    position: relative;
    display: block;
    width: 22.8rem;
    font-size: 2.2rem;
    font-weight: 300;
    text-transform: uppercase;
    color: #de4d86;
    padding: 1.3rem 1.8rem;
    border: 2px solid #de4d86;
}
.header__link:hover {
    color: #fff;
    border: 2px solid #fff;
    text-decoration: none;
}
.header__link:visited {
    text-decoration: none;
}
.header__link .fa {
    position: absolute;
    top: 0.8rem;
    right: 1.8rem;
    font-size: 4rem;
}
@media only screen and (min-width: 768px) {
    .header__picture {
        padding-top: 20rem;
        margin-bottom: 0;
    }
}
@media only screen and (min-width: 992px) {
    .header__picture {
        padding-top: 9rem;
    }
}
.header__tooltip {
    display: none;
    position: absolute;
}
@media only screen and (min-width: 768px) {
    .header__tooltip {
        display: block;
        top: -16rem;
        left: 4.5rem;
    }
}
.header__ipad {
    position: relative;
    display: none;
    left: 0;
    opacity: 0;
}
@media only screen and (min-width: 320px) {
    .header__ipad {
        position: relative;
        top: 4rem;
        padding: 0 3.5rem;
        margin-bottom: 7rem;
    }
}
@media only screen and (min-width: 768px) {
    .header__ipad {
        position: absolute;
        width: 100%;
        top: 24rem;
        padding: 0 5rem;
        margin-bottom: 0;
    }
}
@media only screen and (min-width: 992px) {
    .header__ipad {
        width: 70rem;
        top: 16rem;
        padding: 0 7rem 0 10rem;
        margin-left: -4rem;
    }
}
@media only screen and (min-width: 1200px) {
    .header__ipad {
        top: 19.2rem;
        width: 98rem;
        padding: 0 8.5rem 0 11.5rem;
        margin-left: -16rem;
    }
}
.header__ipad img {
    width: 100%;
}
@media only screen and (min-width: 768px) {
    .header svg {
        margin-top: 0;
    }
}
@media only screen and (min-width: 992px) {
    .header svg {
        width: 70rem;
        margin-left: -4rem;
    }
}
@media only screen and (min-width: 1200px) {
    .header svg {
        width: 98rem;
        margin-left: -16rem;
    }
}
.header svg path {
    fill: none;
    stroke: #fff;
    stroke-width: 2.5px;
}
.header__go-down {
    position: absolute;
    text-align: center;
    bottom: 5rem;
    left: 50%;
    margin-left: -9rem;
}
.header__go-down .fa {
    display: inline-block;
    vertical-align: top;
    font-size: 6rem;
    color: #fff;
    margin-bottom: 1.5rem;
}
.header__go-down p {
    max-width: 100%;
    font-size: 2.2rem;
    font-weight: 300;
    color: #fff;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 0;
}

.top-navigation {
    margin-bottom: 9rem;
}
@media only screen and (min-width: 320px) {
    .top-navigation {
        margin-bottom: 2rem;
    }
}
@media only screen and (min-width: 768px) {
    .top-navigation {
        margin-bottom: 4rem;
    }
}
@media only screen and (min-width: 992px) {
    .top-navigation {
        margin-bottom: 9rem;
    }
}
@media only screen and (min-width: 320px) {
    .top-navigation--left {
        text-align: center;
    }
}
@media only screen and (min-width: 768px) {
    .top-navigation--left {
        text-align: left;
    }
}
@media only screen and (min-width: 320px) {
    .top-navigation--right {
        padding-top: 2rem;
        text-align: center;
    }
}
@media only screen and (min-width: 992px) {
    .top-navigation--right {
        padding-top: 0;
        text-align: left;
    }
}

.logo-top {
    display: inline-block;
    vertical-align: middle;
}
@media only screen and (min-width: 768px) {
    .logo-top {
        margin-right: 3rem;
    }
}

.main-menu {
    display: inline-block;
    vertical-align: middle;
}
@media only screen and (min-width: 320px) {
    .main-menu {
        width: 100%;
    }
}
@media only screen and (min-width: 768px) {
    .main-menu {
        width: 85%;
    }
}
.main-menu__list {
    display: block;
    padding: 0;
    margin: 0;
    list-style-type: none;
    border-bottom: 1px solid #d84b7e;
}
.main-menu__item {
    display: inline-block;
    vertical-align: middle;
    padding: 0 1.4rem;
    margin-bottom: 0;
}
@media only screen and (min-width: 320px) {
    .main-menu__item {
        padding: 0 0.5rem;
    }
}
@media only screen and (min-width: 768px) {
    .main-menu__item {
        padding: 0 1.4rem;
    }
}
@media only screen and (min-width: 768px) {
    .main-menu__item:first-child {
        padding-left: 0;
    }
}
.main-menu__link {
    display: block;
    line-height: 1.8rem;
    font-weight: 300;
    color: #fff;
    margin-bottom: 0;
    padding: 8px 0;
}
@media only screen and (min-width: 320px) {
    .main-menu__link {
        border-bottom: 4px solid transparent;
    }
}
@media only screen and (min-width: 768px) {
    .main-menu__link {
        border-bottom: 8px solid transparent;
    }
}
@media only screen and (min-width: 320px) {
    .main-menu__link {
        font-size: 1.4rem;
    }
}
@media only screen and (min-width: 768px) {
    .main-menu__link {
        font-size: 1.8rem;
    }
}
.main-menu__link:hover, .main-menu__link.active {
    color: #fff;
    text-decoration: none;
}
@media only screen and (min-width: 320px) {
    .main-menu__link:hover, .main-menu__link.active {
        border-bottom: 4px solid #d84b7e;
    }
}
@media only screen and (min-width: 768px) {
    .main-menu__link:hover, .main-menu__link.active {
        border-bottom: 8px solid #d84b7e;
    }
}

.langs {
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    padding: 0;
    list-style-type: none;
}
@media only screen and (min-width: 320px) {
    .langs {
        margin-right: 3rem;
    }
}
@media only screen and (min-width: 992px) {
    .langs {
        padding-top: 1rem;
        padding-left: 2.5rem;
        margin-right: 0;
    }
}
.langs__item {
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 0;
    padding: 1px 4px 1px 3px;
    border-right: 1px solid #fff;
}
.langs__item:last-child {
    border-right: none;
}
.langs__link {
    display: block;
    font-size: 1.4rem;
    font-weight: 300;
    color: #fff;
    margin-bottom: 0;
}
.langs__link:hover {
    color: #fff;
    text-decoration: underline;
}

.shop {
    display: inline-block;
    vertical-align: middle;
    font-size: 1.8rem;
    font-weight: 300;
    color: #fff;
    padding: 5px 15px;
    border: 2px solid #d9498a;
    margin-bottom: 0;
}
@media only screen and (min-width: 992px) {
    .shop {
        float: right;
    }
}
.shop:hover {
    color: #fff;
    text-decoration: none;
    background: #d9498a;
}

.features {
    padding: 7rem 0;
}
@media only screen and (min-width: 320px) {
    .features__item {
        margin-bottom: 2rem;
    }
}
@media only screen and (min-width: 992px) {
    .features__item {
        margin-bottom: 0;
    }
}
.features__icon {
    width: 19rem;
    height: 19rem;
    border-radius: 50%;
    margin: 0 auto 2.2rem;
    padding: 1.8rem;
}
.features__icon--pink {
    border: 2px solid #ed94b4;
}
.features__icon--blue {
    border: 2px solid #b6ebf2;
}
.features__icon--green {
    border: 2px solid #acf2ce;
}
.features__icon--yellow {
    border: 2px solid #f3f4c7;
}
.features__name {
    font-size: 1.6rem;
    line-height: 2.2rem;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 0;
}
.features__name--pink {
    color: #9b1a48;
}
.features__name--blue {
    color: #22aaba;
}
.features__name--green {
    color: #1db567;
}
.features__name--yellow {
    color: #d9da48;
}
.features p {
    font-weight: 300;
    text-align: center;
    line-height: 2.2rem;
}

.cases {
    position: relative;
    min-height: 45.5rem;
    text-align: center;
    background: #eee;
    margin: 5rem 0;
}
.cases h2 {
    display: inline-block;
    vertical-align: top;
    position: relative;
    font-size: 2.2rem;
    font-weight: 300;
    text-align: center;
    text-transform: uppercase;
    color: #888;
    margin-top: -1.8rem;
    margin-bottom: 8.3rem;
    z-index: 9;
    padding: 1.3rem 5.4rem;
    border: 2px solid #737274;
}
.cases h2:after, .cases h2:before {
    display: block;
    content: '';
    position: absolute;
}
.cases h2:after {
    top: -5.7rem;
    left: 50%;
    width: 1px;
    height: 5.6rem;
    background: #888;
}
.cases h2:before {
    top: -7.9rem;
    left: 50%;
    width: 2.2rem;
    height: 2.2rem;
    border-radius: 50%;
    border: 1px solid #888;
    margin-left: -1.1rem;
}
.cases:before {
    background: #eee;
}
.cases:after {
    position: absolute;
    display: block;
    content: '';
    width: 104%;
    bottom: -5rem;
    height: 10rem;
    background: #eee;
    border-radius: 50%;
}
@media only screen and (min-width: 320px) {
    .cases:after {
        width: 120%;
        left: -10%;
    }
}
@media only screen and (min-width: 768px) {
    .cases:after {
        width: 110%;
        left: -5%;
    }
}
@media only screen and (min-width: 992px) {
    .cases:after {
        width: 104%;
        left: -2%;
    }
}
@media only screen and (min-width: 992px) {
    .cases__carousel {
        max-width: 84%;
        margin: 0 auto;
    }
}
.cases__item h3 {
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 2.2rem;
    text-transform: uppercase;
    color: #9b1a48;
    margin-bottom: 2.2rem;
}
.cases__item p {
    font-size: 1.6rem;
    font-weight: 300;
    line-height: 2.2rem;
    margin-bottom: 2.2rem;
}
.cases__item a {
    font-size: 1.6rem;
    font-weight: 700;
    text-transform: uppercase;
    color: #9b1a48;
}
.cases__content {
    text-align: left;
    padding-top: 2.8rem;
}

.owl-dots {
    padding-top: 5rem;
    text-align: center;
}

.owl-dot {
    display: inline-block;
    vertical-align: top;
    width: 1rem;
    height: 1rem;
    border: 1px solid #4d4c4f;
    border-radius: 50%;
    margin: 0 3px;
}
.owl-dot.active {
    background: #9b1a48;
    border: 1px solid #9b1a48;
}

.service {
    width: 100%;
    height: 39rem;
    background: url('../images/bg-pink.jpg') repeat top center;
    margin-top: -5rem;
    padding: 15rem 0;
}
.service__headline {
    max-width: 86rem;
    font-size: 3rem;
    font-weight: 300;
    color: #fff;
    line-height: 3.2rem;
    text-align: center;
    text-transform: uppercase;
    margin: 0 auto;
}
@media only screen and (min-width: 320px) {
    .service {
        height: auto;
    }
}
@media only screen and (min-width: 992px) {
    .service {
        background-size: 100% auto;
    }
}

.contacts {
    position: relative;
}
@media only screen and (min-width: 768px) {
    .contacts {
        margin-bottom: -7.5rem;
    }
}
@media only screen and (min-width: 992px) {
    .contacts {
        margin-bottom: -11.5rem;
    }
}
.contacts:before {
    background: #fff;
}
.contacts__img {
    display: block;
    margin: 0 auto;
}
.contacts__headline {
    font-size: 3rem;
    font-weight: 300;
    text-transform: uppercase;
    margin-bottom: 1.4rem;
}
.contacts__text {
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 2.4rem;
    margin-bottom: 2.4rem;
}
.contacts__item {
    max-width: 18.6rem;
    font-size: 1.4rem;
    font-weight: 300;
    color: #9b1a48;
    padding: 7px 9px;
    border: 2px solid #eee;
    margin-bottom: 1.4rem;
}
.contacts__place {
    font-size: 1.8rem;
    text-transform: uppercase;
    color: #9b1a48;
    margin-bottom: 2rem;
}
.contacts__address {
    font-weight: 300;
}
.contacts__address span {
    display: block;
}
.contacts img {
    margin-bottom: 2.5rem;
}

.social {
    padding: 0;
    padding-top: 1.1rem;
    margin: 0;
    list-style-type: none;
}
.social__item {
    display: inline-block;
    vertical-align: top;
    margin-right: 1rem;
}
.social__item:last-child {
    margin-right: 0;
}
.social__link {
    display: block;
    width: 3.5rem;
    height: 3.5rem;
    text-align: center;
    line-height: 3.5rem;
    background: #950b3f;
    border-radius: 50%;
}
.social__link:hover .fa {
    font-size: 1.8rem;
}
.social .fa {
    color: #fff;
    -webkit-transition: all 0.2s ease-in-out;
       -moz-transition: all 0.2s ease-in-out;
        -ms-transition: all 0.2s ease-in-out;
         -o-transition: all 0.2s ease-in-out;
            transition: all 0.2s ease-in-out;
}

.footer {
    position: relative;
    width: 100%;
    height: 9.2rem;
    text-align: center;
    background: #eee;
    margin-top: 5rem;
}
.footer__logo {
    display: inline-block;
    position: relative;
    z-index: 99;
    margin: 0;
    margin-top: -4rem;
}
.footer:before {
    background: #eee;
}